body {
  font-family: "lucida grande", "lucida sans unicode", lucida, helvetica, "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
  background-color: var(--background-color);
}
.site-content {
  padding-top: 48px;
  margin-bottom: 48px;
  overflow-x: hidden;
}
a {
  color: #179BB2;
  transition: all ease 0.3s;
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  color: #179BB2;
  text-decoration: none;
}
a:active {
  text-decoration: none;
}
a:focus {
  outline: none;
}
#show_image_layer {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  z-index: 1000;
  background: #dedede;
  opacity: 0.75;
}
#img_wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1001;
}
#img_wrapper img {
  cursor: zoom-out;
  max-width: 100%;
}
/*深色*/
@media (prefers-color-scheme: dark) {
  :root {
    /* 文字颜色 */
    --font-post-title-color: #a8a8a8;
    --font-post-subtitle-color: #959595;
    --font-post-content-color: #838383;
    --font-link-color: #179bb2;
    --font-link-active-color: #179bb2;
    /* 边框 */
    --border-color: #525252;
    --border-shadow-color: #5b5b5b;
    --border-divider-color: #4b4b4b;
    /* 背景 */
    --background-color: #242424;
    --active-background-color: #070505;
    --background-model-color: #454545;
    --table-header-background-color: #454545;
    --table-single-background-color: #383838;
    --table-double-background-color: #2c2c2c;
    /* 文章里的代码容器背景 */
    --post-code-box-background-color: #2f2f2f;
    /* 版权背景 */
    --copyright-edge-background-color: #454545;
    --copyright-center-background-color: #333333;
  }
}
/*浅色*/
@media (prefers-color-scheme: light) {
  :root {
    /* 文字颜色 */
    --font-post-title-color: #242424;
    --font-post-subtitle-color: #6e6e6e;
    --font-post-content-color: #999;
    --font-link-color: #179bb2;
    --font-link-active-color: #179bb2;
    /* 边框 */
    --border-color: #4b4b4b;
    --border-shadow-color: #E9E9E9;
    --border-divider-color: #d5d5d5;
    /* 背景 */
    --background-color: #fff;
    --active-background-color: #F5F5F5;
    --background-model-color: #f8f8f8;
    --table-header-background-color: #f6f6f6;
    --table-single-background-color: #ececec;
    --table-double-background-color: #e5e5e5;
    /* 文章里的代码容器背景 */
    --post-code-box-background-color: #e5e5e5;
    /* 版权背景 */
    --copyright-edge-background-color: #e1f2fb;
    --copyright-center-background-color: #f1f9f9;
  }
}
/* 分辨率适配 */
/*----------------------------向下的指示器----------------------------*/
.indicator {
  display: block;
  position: absolute;
  cursor: pointer;
  bottom: 0;
  font-size: 56px;
  text-align: center;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.6);
  -webkit-animation: bounce 4s 2s infinite;
  animation: bounce 4s 0.4s infinite;
}
.indicator:hover {
  color: #EFEFEF;
}
/*bounce动画*/
@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
  }
  60% {
    -webkit-transform: translateY(-5px);
    transform: translateY(-5px);
  }
}
/*----------------------------------分页器-------------------------------------*/
.post-list-pagination {
  padding: 0 10px;
  margin-top: 30px;
}
.post-list-pagination .post-navigation {
  margin: 0 15px;
}
.post-list-pagination .post-navigation ul {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
  color: var(--font-post-content-color);
}
.post-list-pagination .post-navigation ul li {
  display: inline-block;
  font-family: "Josefin Sans", sans-serif;
  font-size: 12px;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-weight: bold;
  word-spacing: 2px;
}
.post-list-pagination .post-navigation ul li.nav-previous {
  float: left;
  margin-top: 2px;
}
.post-list-pagination .post-navigation ul li.nav-next {
  float: right;
  margin-top: 2px;
}
.post-list-pagination .post-navigation ul li a {
  color: var(--font-post-content-color);
  padding: 0 2px;
}
.post-list-pagination .post-navigation ul li a:hover {
  color: var(var(---font-link-color));
  text-decoration: none;
}
.post-list-pagination .post-navigation ul li a span {
  padding: 0 8px;
}
.prev-next-wrap {
  margin: 30px auto;
  display: flex;
  max-width: 1000px;
}
.prev-next-wrap .prev-next-item {
  display: flex;
  flex: 1 0;
  justify-content: center;
}
.prev-next-wrap .prev-next-item .prev-next-content {
  margin-left: 20px;
}
.prev-next-wrap .prev-next-item .prev-next-content .link {
  color: #fff;
  background-color: #179bb2;
  padding: 0 10px;
  border-radius: 10px;
  display: inline-block;
  margin-bottom: 10px;
}
.prev-next-wrap .prev-next-item .prev-next-content .title {
  display: block;
}
.prev-next-wrap .prev-next-item .prev-next-content .date {
  font-style: italic;
  font-family: consolas;
  color: #909090;
  margin-top: 10px;
}
@media (max-width: 500px) {
  .prev-next-wrap {
    display: none;
  }
}
/*-----------------------------推荐阅读-------------------------------*/
.read-next {
  display: flex;
  align-items: stretch;
  margin: 30px auto;
  padding-bottom: 60px;
  max-width: 960px;
  font-family: '-apple-system', 'Open Sans', 'Microsoft YaHei', sans-serif;
}
.read-next-story {
  padding: 16px;
  background-color: #fff;
  border: 1px solid #ecf0f1;
  display: flex;
  flex-direction: column;
  min-width: 50%;
  margin: 0 15px;
}
.read-next-story .read-next-media {
  overflow: hidden;
  background-color: #f4f6f8;
}
.read-next-story .read-next-media a {
  background: #f4f6f8 no-repeat center center;
  background-size: cover;
  position: relative;
  transition: all 0.5s ease-in-out;
  display: block;
}
.read-next-story .read-next-media a:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(56, 56, 56, 0.25), rgba(56, 56, 56, 0.35) 100%);
  opacity: 0.4;
}
.read-next-story .read-next-media a:after {
  display: block;
  content: '';
  width: 100%;
  padding-bottom: 300px;
}
.read-next-story .read-next-media a:hover:before {
  opacity: 0.8;
}
.read-next-story .read-next-media a:hover {
  transform: scale(1.01);
}
.read-next-story .read-next-content h3 {
  font-size: 18px;
  font-weight: 800;
  margin-bottom: 12px;
}
.read-next-story .read-next-content h3 a {
  color: #333;
}
.read-next-story .read-next-content h3 a:hover {
  color: var(var(---font-link-color));
}
.read-next-story .read-next-content p {
  color: var(--font-post-content-color);
  line-height: 1.7;
}
.site-social-navigation {
  margin-top: 40px;
}
.site-social-navigation ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}
.site-social-navigation ul li {
  margin: 0 10px;
}
.site-social-navigation ul li a {
  color: #a7afb3;
  font-size: 24px;
  border-radius: 100%;
  border: solid 2px rgba(255, 255, 255, 0.35);
  display: inline-block;
  text-align: center;
  width: 48px;
  height: 48px;
  line-height: 48px;
}
.site-social-navigation ul li a:hover {
  border: solid 2px rgba(255, 255, 255, 0.65);
}
.footer-follow-social {
  text-align: left;
  list-style: none;
  display: flex;
  padding-left: 0;
}
.footer-follow-social a {
  font-size: 18px;
  margin: 0 18px 0 12px;
  color: var(--font-post-content-color);
}
.footer-follow-social a:hover {
  color: var(var(---font-link-color));
}
.post-author-social {
  list-style: none;
  display: flex;
  justify-content: center;
  padding: 0;
}
.post-author-social li {
  padding: 6px 18px;
}
.post-author-social li a {
  font-size: 24px;
  color: var(--font-post-content-color);
}
.post-author-social li a:hover {
  color: #333;
}
.author-page header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.author-page .author-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.author-page .author-container .plane-wrapper {
  position: relative;
}
.author-page .author-container .plane-wrapper .rotate-container {
  position: absolute;
  top: 52%;
  left: 12.5%;
  animation: rotating 0.2s linear infinite reverse;
}
.author-page .author-container .about-me-content {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 40px;
}
@keyframes rotating {
  0% {
    -webkit-transform: rotate(0) rotateY(-20deg);
    transform: rotate(0) rotateY(-20deg);
  }
  50% {
    -webkit-transform: rotate(180deg) rotateY(-20deg);
    transform: rotate(180deg) rotateY(-20deg);
  }
  100% {
    -webkit-transform: rotate(360deg) rotateY(-20deg);
    transform: rotate(360deg) rotateY(-20deg);
  }
}
@keyframes btn-pudding {
  12%,
  15% {
    transform: scale(0.93, 1.07);
  }
  16%,
  17% {
    transform: scale(0.94, 1.06);
  }
  10%,
  18% {
    transform: scale(0.95, 1.05);
  }
  19% {
    transform: scale(0.96, 1.04);
  }
  20% {
    transform: scale(0.98, 1.02);
  }
  21%,
  40%,
  48% {
    transform: scale(0.99, 1.01);
  }
  22%,
  23%,
  37%,
  39%,
  49% {
    transform: scale(1, 1);
  }
  7%,
  24%,
  34%,
  36% {
    transform: scale(1.01, 0.99);
  }
  25%,
  27%,
  30%,
  33% {
    transform: scale(1.02, 0.98);
  }
  28%,
  29% {
    transform: scale(1.03, 0.97);
  }
  2% {
    transform: scale(1.16, 0.84);
  }
  0% {
    transform: scale(1.2, 0.8);
  }
}
.cd-top {
  display: none;
  position: fixed;
  bottom: 40px;
  right: 20px;
  opacity: 0;
  transition: opacity 0.3s 0s, visibility 0s 0.3s;
}
.cd-top svg {
  width: 64px;
  height: 64px;
  fill: var(--font-post-content-color);
}
.cd-top:hover {
  animation: btn-pudding 1s linear;
}
.cd-top.cd-is-visible,
.cd-top.cd-fade-out,
.cd-top:hover {
  transition: opacity 0.3s 0s, visibility 0s 0s;
}
.cd-top.cd-is-visible {
  /* the button becomes visible */
  visibility: visible;
  opacity: 0.8;
}
.cd-top.cd-fade-out {
  /* if the user keeps scrolling down, the button is out of focus and becomes less visible */
  opacity: 0.8;
}
@media only screen and (min-width: 768px) {
  .cd-top {
    right: 20px;
    bottom: 20px;
  }
}
@media only screen and (min-width: 1024px) {
  .cd-top {
    right: 50px;
    bottom: 50px;
  }
}
