.copyright {
  background-image: linear-gradient(to right, var(--copyright-edge-background-color) 1%, var(--copyright-center-background-color) 50%, var(--copyright-edge-background-color) 100%);
  padding: 16px 0;
  color: var(--font-post-content-color);
  text-align: center;
}
.copyright p {
  margin: 0;
  line-height: 1.8;
  font-size: 13px;
}
.all-rights-reserved {
  margin-left: 5px;
}
.site-footer {
  color: var(--font-post-content-color);
  background-color: var(--background-color);
  position: relative;
  text-align: center;
  padding: 24px 0;
}
.footer-content-title {
  text-align: left;
  margin-bottom: 30px;
}
.recent-single-post {
  display: flex;
  align-items: flex-start;
  margin-bottom: 18px;
}
.recent-single-post:last-child {
  margin-bottom: 0;
}
.footer-post-cover {
  width: 80px;
  height: 80px;
  display: inline-block;
  background: no-repeat center center;
  background-size: cover;
  border-radius: 4px;
  flex-shrink: 0;
}
.footer-post-info {
  margin-left: 16px;
  text-align: left;
}
.footer-post-info .footer-post-title {
  line-height: 24px;
  font-size: 16px;
  margin: 0 0 12px;
}
.footer-post-info .footer-post-title a {
  color: var(--font-post-content-color);
  word-wrap: normal;
  word-break: break-all;
}
.footer-post-info .footer-post-title a:hover {
  color: var(var(---font-link-color));
}
.footer-post-info .footer-post-date {
  font-size: 12px;
}
.footer-content-tags {
  text-align: left;
}
.footer-content-tags a {
  color: var(--font-post-content-color);
  padding: 0 16px;
  border: 1px solid #1F2F3D;
  display: inline-block;
  margin-right: 8px;
  margin-bottom: 8px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  background-image: none !important;
}
.footer-content-tags a:hover {
  background: var(var(---font-link-color));
}
.footer-content-tags a .overlay h2 {
  font-size: 14px;
  line-height: 24px;
  margin: 0;
}
.footer-content-tags a .overlay button {
  display: none;
}
.footer-friend-links {
  text-align: left;
  margin-bottom: 30px;
}
.footer-friend-links a {
  color: var(--font-post-content-color);
  padding: 4px 12px;
  font-size: 14px;
  line-height: 24px;
  margin: 0 8px 8px 0;
  border-radius: 2px;
  display: inline-block;
}
.footer-friend-links a:hover {
  background: var(var(---font-link-color));
}
.site-header {
  background: no-repeat center center;
  background-size: cover;
  position: relative;
  height: 100vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}
.site-header:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(56, 56, 56, 0.45), rgba(56, 56, 56, 0.75) 100%);
}
.header-top {
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  background-color: rgba(14, 21, 27, 0.6);
}
.header-middle {
  display: flex;
  height: 100%;
  align-items: center;
}
.header-middle-content {
  text-align: center;
  position: relative;
}
.site-title,
.site-description {
  font-weight: lighter;
  color: #bfbfbf;
  text-shadow: 0 1px 1px #595959;
  letter-spacing: 6px;
  padding-top: 20px;
}
.site-title {
  font-size: 65px;
}
.header-middle-content a:hover,
.site-description:hover {
  color: #efefef;
  text-shadow: 0 1px 1px #000;
  text-decoration: none;
}
.tag-template .site-content {
  min-height: 52vh;
}
.site-content .post {
  margin-bottom: 30px;
  padding: 0 15px;
}
.post-card {
  box-shadow: 0 2px 5px 0 var(--border-divider-color);
  transition: all 0.4s ease;
}
.post-card:hover {
  box-shadow: 0 2px 8px 4px var(--border-shadow-color);
  transform: translate(0, -5px);
}
.post-cover {
  position: relative;
  overflow: hidden;
  height: 260px;
  width: 100%;
}
.post-cover .cover-image {
  display: block;
  background: no-repeat center center;
  background-size: cover;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.post-cover .cover-image:after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(56, 56, 56, 0) 0, rgba(56, 56, 56, 0.25) 100%);
  transition: opacity 1s ease-in-out;
}
.post-cover:hover .cover-image {
  transform: scale(1.04);
}
.post-cover:hover .cover-image:after {
  background: linear-gradient(to bottom, rgba(56, 56, 56, 0) 0, rgba(56, 56, 56, 0.45) 100%);
}
.post-cover .cover-transition {
  transition: transform 1s linear, opacity 1s ease-in-out;
}
.cover-bottom {
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  background: linear-gradient(transparent, #2E2E2E);
  padding: 20px 0;
}
.cover-bottom .post-tags {
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 0 8px;
}
.cover-bottom .post-tags a {
  margin: 0 5px 5px 0;
  padding: 0 6px;
  border-radius: 2px;
  background: rgba(255, 255, 255, 0.35);
  color: #dadada;
  font-size: 13px;
  display: inline-block;
}
.cover-bottom .post-tags a:hover {
  text-decoration: none;
  background: rgba(255, 255, 255, 0.25);
}
/*post详情*/
.post-info {
  background-color: var(--toc-active-bg-color);
  position: relative;
  overflow: hidden;
  padding: 20px;
  height: 210px;
  font-family: '-apple-system', 'Open Sans', 'Microsoft YaHei', sans-serif;
}
.post-info .post-title {
  font-size: 18px;
  margin: 0;
  line-height: 1.42857143;
  font-weight: 800;
}
.post-info .post-title a {
  color: var(--font-post-title-color);
}
.post-info .post-title a:hover {
  color: var(var(---font-link-color));
  text-decoration: none;
}
.post-info .post-summary {
  line-height: 1.6;
  color: var(--font-post-content-color);
  margin: 5px 0;
  word-break: break-all;
  word-wrap: normal;
}
.post-info .post-date {
  position: absolute;
  bottom: 0;
  padding: 15px 0;
  color: #717f8e;
  font-size: 12px;
}
.main-nav .menu {
  margin: 0;
  padding: 0;
  list-style: none;
}
ul.menu li {
  color: var(--font-post-content-color);
  display: inline;
  position: relative;
}
ul.menu li a {
  font-weight: normal;
  color: inherit;
  display: inline-block;
  font-size: 13px;
  line-height: 20px;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  padding: 15px;
}
ul.menu li a:hover {
  color: #efefef;
  text-shadow: 0 1px 1px #000;
  text-decoration: none;
}
/* toc 侧边栏 */
.navbar-side {
  padding-top: 5vh;
  width: 240px;
  height: 100%;
  transition: all 0.2s;
}
.navbar-side .catalog {
  padding-bottom: 1em;
}
.navbar-side .catalog-toggle::before {
  content: "−";
  position: relative;
  margin-right: 5px;
  bottom: 1px;
}
/*-------------------------文章toc目录---------------------------*/
.toc-wrapper {
  width: 240px;
}
.toc-wrapper.position-absolute {
  position: absolute;
}
.toc-wrapper.position-fixed {
  position: fixed;
  top: 0;
}
#toc {
  font-size: 14px;
  padding-left: 5px;
}
#toc a {
  color: var(--font-post-subtitle-color) !important;
}
#toc a:hover,
#toc a:active {
  color: var(var(---font-link-color)) !important;
}
#toc ul {
  position: relative;
  list-style: none;
  height: auto;
  overflow: hidden;
  padding-left: 0;
  padding-right: 5px;
  text-indent: 0;
}
#toc ul li {
  position: relative;
  list-style: none;
}
#toc ul li a {
  padding-left: 10px;
  max-width: 230px;
  display: inline-block;
  vertical-align: middle;
  height: 30px;
  line-height: 30px;
  overflow: hidden;
  text-decoration: none;
  white-space: nowrap;
  text-overflow: ellipsis;
}
#toc ul .toc-h2 {
  margin-left: 0;
  font-size: 13px;
  font-weight: 700;
}
#toc ul .toc-h3 {
  margin-left: 13px;
  font-size: 12px;
}
#toc ul .toc-h3 a {
  max-width: 220px;
}
#toc ul .toc-active {
  border-radius: 4px;
  background-color: var(--active-background-color);
}
#toc ul .toc-active a {
  color: #0085a1 !important;
}
@media (max-width: 500px) {
  /* 侧边栏 */
  .navbar-side {
    display: none;
  }
}
.post-template:not(.page-template) .site-content {
  margin-bottom: 0;
  padding-top: 10px;
}
/* header */
.post-header {
  background: no-repeat center center;
  background-size: cover;
  position: relative;
  height: 320px;
}
.post-header:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(56, 56, 56, 0.75), rgba(56, 56, 56, 0.35) 100%);
}
.post-header .post-header-container {
  position: relative;
  width: 80%;
  height: 100%;
  color: #dadada;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
}
.post-header .post-nav {
  position: absolute;
  top: 45px;
  left: 0;
}
.post-header .post-nav a {
  color: #dadada;
  font-size: 12px;
  padding: 8px 32px;
  opacity: 0.8;
  border: 1px solid #dedede;
  border-radius: 0;
  background: none;
}
.post-header .post-nav a:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.6);
}
.post-header .post-content {
  text-align: left;
  margin-top: 45px;
}
.post-header .post-content .article-header {
  transition: all 0s;
  padding: 15px 24px;
  margin: 0 auto;
  max-width: 996px;
  color: #fff0;
  background-image: linear-gradient(to right, #e6e6e6, #c1dfe5);
  background-clip: text;
}
.post-header .post-content .article-header .post-title {
  font-weight: 500;
  font-size: 40px;
  word-wrap: break-word;
}
.post-header .post-content .article-header .post-attr {
  font-weight: 300;
  font-size: 20px;
}
.post-header .post-content .article-header .post-attr .dotted {
  margin: 0 4px;
}
.post-header .post-content .post-tags {
  position: absolute;
  left: 0;
  bottom: 1rem;
  font-size: 12px;
  text-decoration: none;
  margin-bottom: 6px;
}
.post-header .post-content .post-tags a {
  display: inline-block;
  border: 1px solid rgba(255, 255, 255, 0.8);
  border-radius: 999em;
  margin-right: 5px;
  line-height: 22px;
  padding: 0 10px;
  color: #dadada;
}
.post-header .post-content .post-tags a:hover {
  color: #fff;
  text-shadow: none;
  background: hsla(0, 0%, 100%, 0.2);
  border-color: hsla(0, 0%, 100%, 0.6);
}
.post-header .post-content .post-tags ul {
  list-style: none;
}
.post-header .post-content .post-tags ul li {
  display: inline-block;
  margin-right: 15px;
}
.post-header .post-content .post-tags ul li i {
  font-size: 12px;
  margin-right: 5px;
}
/* 主体容器 */
.article-wrapper {
  display: flex;
  margin: 0 auto;
  transition: all 0s;
  /* 文章 */
}
.article-wrapper .article-left {
  transition: all 0s;
  margin: 0 auto;
  width: 100%;
  max-width: 1040px;
}
.copy-code-wrapper {
  position: absolute;
  color: #fff !important;
  text-decoration: none;
  background: rgba(0, 0, 0, 0.65);
  font-size: 12px;
  padding: 0 6px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  cursor: pointer;
  width: 45px;
  text-align: center;
}
.copy-code-wrapper:hover {
  text-decoration: none !important;
}
.post-footer {
  position: relative;
  margin-top: 80px;
}
.post-footer .post-author {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  position: relative;
  border-top: 1px dashed var(--border-divider-color);
}
.post-footer .post-author .author-image {
  box-sizing: border-box;
  display: block;
  position: absolute;
  top: -40px;
  left: 50%;
  margin-left: -40px;
  width: 80px;
  height: 80px;
  border-radius: 100%;
  overflow: hidden;
  padding: 6px;
  background: var(--background-model-color);
  z-index: 2;
  box-shadow: var(--border-divider-color) 0 0 0 1px;
}
.post-footer .post-author .author-image a {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  border-radius: 100%;
}
.post-footer .post-author .author-info {
  margin: 48px 0 24px 0;
  text-align: center;
  width: 55%;
  max-width: 900px;
}
.post-footer .post-author .author-info h4 {
  color: var(--font-post-subtitle-color);
}
.post-footer .post-author .author-info h4 a {
  color: var(--font-post-subtitle-color);
  text-decoration: none;
}
.post-footer .post-author .author-info h4 a:hover {
  color: var(--font-post-title-color);
}
.post-footer .post-author .author-info p {
  margin: 20px 0;
  line-height: 1.9;
  color: var(--font-post-subtitle-color);
}
.post-footer .post-author .author-info .author-meta {
  margin: 20px 0 15px;
  font-size: 14px;
  line-height: 1;
  font-style: italic;
  color: var(--font-post-subtitle-color);
}
.post-footer .post-author .author-info .author-meta a {
  color: var(--font-post-subtitle-color);
}
.post-footer .post-author .author-info .author-meta a:hover {
  color: var(--font-post-title-color);
}
.post-footer .post-author .author-info .author-meta span {
  display: inline-block;
  margin-right: 20px;
  word-wrap: break-word;
  line-height: 1.9;
}
.post-footer .post-author .author-info .author-meta span i {
  margin-right: 5px;
}
/* 评论 */
.vcomments {
  margin: 1vw;
}
@media (max-width: 500px) {
  .post-header .post-header-container {
    width: 95%;
  }
  .post-header .post-content {
    margin-top: 15px;
  }
  .post-header .post-content .article-header .post-title {
    font-size: 40px;
  }
  .post-header .post-content .article-header .post-attr {
    font-size: 17px;
  }
  .post-header .post-content .post-tags {
    left: 0;
    bottom: 2px;
  }
}
/*标签云*/
.page-tag_cloud .site-content a {
  width: 220px;
  height: 135px;
  display: flex;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  position: relative;
  margin: 15px;
}
.page-tag_cloud .site-content a .overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  text-align: center;
  background-color: rgba(75, 75, 75, 0.55);
}
.page-tag_cloud .site-content a:hover .overlay {
  background-color: rgba(48, 152, 157, 0.4);
}
.page-tag_cloud .site-content a .overlay h2 {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  position: relative;
  font-size: 16px;
  padding: 10px;
  background: rgba(0, 0, 0, 0.6);
  transform: translateY(80px);
  transition: all 0.4s ease-in-out;
}
.page-tag_cloud .site-content a:hover .overlay h2 {
  transform: translateY(15px);
}
.page-tag_cloud .site-content a .overlay button {
  opacity: 0;
  transform: scale(0);
  transition: all 0.4s ease-in-out;
  font-weight: normal;
  border: 1px solid #fff;
  padding: 82px 0 12px;
  display: inline-block;
  text-decoration: none;
  left: 8%;
  top: 10px;
  width: 84%;
  position: absolute;
  text-transform: uppercase;
  background: transparent;
  color: #fff;
}
.page-tag_cloud .site-content a:hover .overlay button {
  box-shadow: 0 0 5px #fff;
  opacity: 1;
  transform: scale(1);
}
.tags-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
}
.tag-header {
  background: no-repeat center center;
  background-size: cover;
  position: relative;
  height: 48vh;
}
.tag-header:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(56, 56, 56, 0.75), rgba(56, 56, 56, 0.35) 100%);
}
.tag-header-container {
  position: relative;
  width: 80%;
  height: 100%;
  color: var(--font-post-content-color);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
}
.tag-content {
  text-align: center;
  margin-top: 20px;
}
.tag-title {
  color: var(--font-post-content-color);
  font-size: 56px;
  font-weight: lighter;
  text-align: center;
  letter-spacing: 4px;
}
.tag-description {
  font-size: 16px;
}
.tag-post-num {
  position: absolute;
  bottom: 15px;
  width: 100%;
}
.tag-nav {
  position: absolute;
  top: 45px;
  left: 0;
}
.tag-nav a {
  color: var(--font-post-content-color);
  font-size: 12px;
  padding: 8px 32px;
  opacity: 0.8;
  border: 1px solid #fff;
  border-radius: 0;
  background: none;
}
.tag-nav a:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.6);
}
.tag-post-num i {
  margin-right: 10px;
}
