.tag-template .site-content {
  min-height: 52vh;
}

.site-content .post {
  margin-bottom: 30px;
  padding: 0 15px;
}

.post-card {
  box-shadow: 0 2px 5px 0 var(--border-divider-color);
  transition: all 0.4s ease;
}

.post-card:hover {
  box-shadow: 0 2px 8px 4px var(--border-shadow-color);
  transform: translate(0, -5px);
}

.post-cover {
  position: relative;
  overflow: hidden;
  height: 260px;
  width: 100%;
  .cover-image {
    display: block;
    background: no-repeat center center;
    background-size: cover;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    &:after {
      .cover-mixin;
      background: linear-gradient(to bottom, rgba(56, 56, 56, 0) 0, rgba(56, 56, 56, .25) 100%);
      transition: opacity 1s ease-in-out;
    }
  }
  &:hover .cover-image {
    transform: scale(1.04);
    &:after {
      background: linear-gradient(to bottom, rgba(56, 56, 56, 0) 0, rgba(56, 56, 56, .45) 100%);
    }
  }
  .cover-transition {
    transition: transform 1s linear, opacity 1s ease-in-out;
  }
}

.cover-bottom {
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  background: linear-gradient(transparent, #2E2E2E);
  padding: 20px 0;
  .post-tags {
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 0 8px;
    a {
      margin: 0 5px 5px 0;
      padding: 0 6px;
      border-radius: 2px;
      background: rgba(255, 255, 255, .35);
      color: #dadada;
      font-size: 13px;
      display: inline-block;
      &:hover {
        text-decoration: none;
        background: rgba(255, 255, 255, .25);
      }
    }
  }
}

/*post详情*/
.post-info {
  background-color: var(--toc-active-bg-color);
  position: relative;
  overflow: hidden;
  padding: 20px;
  height: 210px;
  font-family: '-apple-system', 'Open Sans', 'Microsoft YaHei', sans-serif;
  .post-title {
    font-size: 18px;
    margin: 0;
    line-height: 1.42857143;
    font-weight: 800;
    a {
      color: var(--font-post-title-color);
      &:hover {
        color: var(var(---font-link-color));
        text-decoration: none;
      }
    }
  }
  .post-summary {
    line-height: 1.6;
    color: var(--font-post-content-color);
    margin: 5px 0;
    word-break: break-all;
    word-wrap: normal;
  }
  .post-date {
    position: absolute;
    bottom: 0;
    padding: 15px 0;
    color: #717f8e;
    font-size: 12px;
  }
}

